



























.timeline {
  list-style: none;
  margin: 0;
  padding: 0;
}
.timeline-item {
  margin: 0!important;
  padding: 0 0 12px;
  list-style: none;
  position: relative;
}
.timeline-item-tail {
  height: 100%;
  border-left: 1px solid #e9eaec;
  position: absolute;
  left: 6px;
  top: 0;
}
.timeline-item-head-default {
  border-color: #C0C0C0;
  color: #C0C0C0;
}
.timeline-item-head-blue {
  border-color: #2d8cf0;
  color: #2d8cf0;
}
.timeline-item-head-green {
  border-color: #19be6b;
  color: #19be6b;
}
.timeline-item-head-red {
  border-color: #ed3f14;
  color: #ed3f14;
}
.timeline-item-head {
  width: 13px;
  height: 13px;
  background-color: #fff;
  border-radius: 50%;
  border: 1px solid;
  position: absolute;
}
.timeline-item-content {
  padding: 1px 1px 10px 24px;
  font-size: 12px;
  position: relative;
  top: -3px;
}
.timeline-item:last-child .timeline-item-tail {
  display: none;
}



