












































































    .menu-content {
    padding: 20px 30px 0px 30px;
    height: auto;
  }
  /* 菜单内容区域菜单样式 */
  .menu {
    display: inline-block;
    margin-left: 30px;
    padding: 5px;
    border: 1px solid rgba(255,255,255,0.6);
    border-radius: 5px;
    text-align: center;
    font-weight: bold;
    width: 160px;
	/*width: 10%;*/
	/*height: 71%;*/
    vertical-align: top;
  }
  .menu li {
    list-style-type: none;
    color: rgba(255,255,255,1);
    font-weight: bold;
    line-height: 50px;
    /*text-align: center;*/
  }
  .alone, .menu li + li {
    /*height: 50px;*/
    border-radius: 5px;
    border: 1px solid rgba(255,255,255,0.3);
    /*padding: 5px;*/
    margin: 10px 6px;
	cursor: pointer;
    font-weight: bold;
    color: rgba(255,255,255,0.8);
    line-height: 45px;
  }
  .alone:hover, .menu li + li:hover {
    background: rgba(255, 255, 255, 0.2);
  }
  /* 如果打开，右上角显示一个小的错号 */
  .alone span, .menu li + li span {
    position: relative;
    top: 0px;
    right: 0px;
    color: #fff;
    float: right;
    margin-left: -14px;
  }
  .alone span:hover, .menu li + li span:hover {
    background: red;
  }
  .opentab {
    background: rgba(255, 255, 255, 0.5);
  }
  /*针对屏幕分辨率在1366*768一些变动*/
  @media screen and (max-width: 1366px) {
    .menu {
        margin-left: 10px;
        width: 150px;
    }
  }



