




























.top-menu button {
  color: #fff;
  background: 000;
  border: 0px;
  margin-right: 10px;
  width: 60px;
  height: auto;
}
.top-menu button:hover {
  background: yellow;
}
.top-menu button:focus {
  background:-webkit-gradient(linear, 0% 0%, 0% 100%,from(#fff), to(#055182));
}
.top-menu img {
  display: block;
  margin: 0 auto;
}



